<script>
import {getData , sendParam} from "../../network/home"
const store = () => import('../../store/index')

var phone = /^1[3456789]\d{9}P$/; //
var account=/^\d{11}$/; //
var password=/^[0-9a-zA-Z]{10,18}$/; //
var rechargePasswordAdd=/^[0-9a-zA-Z]{6,9}$/; //
var money = /^\d{1,100}$/; //

export default {
    name: 'PersonalContent',
    data() {
        //
        var validateName = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            }
        };
        //
        var validateAcco = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            }
        };
        //
        var validatePass = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            }
        };
        //
        var validateNpas = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            }
        };
        //
        var validateRech = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            }else if(value.match(rechargePasswordAdd) == null){
                callback(new Error(''));
            }
        };
        //
        var validateNew = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            }else if(value.match(rechargePasswordAdd) == null){
                callback(new Error('6~9'));
            }
        };
        //
        var validaterLA = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            }
        };
