    methods: {
        // ------------------------------------------------------
    getchat(){
      let url = "/info/Center/userChat";
      let data = {
        id: this.userMessage.id,
      }
      console.log(data)
      sendParam(url,data)
        .then(res => {
          if (res.data.code == 1) {
            console.log(res.data.data)
            for( let item of res.data.data){
              if(item.sender == this.userMessage.id){
                const msgObj = {
                  date: item.chat_time,
                  text: { text: item.content },
                  mine: true,
                  name: this.userMessage.name,
                  img: this.userMessage.head_img
                };
                this.list.push(msgObj);
              }else{
                const msgObj = {
                  date: item.chat_time,
                  text: { text: item.content },
                  mine: false,
                  name: item.name,
                  img: item.head_img
                };
                this.list.push(msgObj);
              }
            }
            
          
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    bindEnter () {
      const msg = this.inputMsg
      if (!msg) return;
      const msgObj = {
        "date": new Date().toLocaleDateString() + new Date().toLocaleTimeString(),
        "text": { "text": msg },
        "mine": true,
        "name": this.userMessage.name,
        "img": this.userMessage.head_img,
      }
      this.list.push(msgObj)
      // nodee
      var sendContainer = {
        type: 'infor',
        sender: this.userMessage.id,
        receiver: this.receiveId,
        container: msg,
        name: this.userMessage.name,
        img: this.userMessage.head_img,
        mine: false
      }
      let params = JSON.stringify(sendContainer);
      console.log(params)
      this.socket.send(params);
    },
    toolEvent (type) {
      console.log('tools', type)
    },
    //-------websocket--
    init() {
      if (typeof WebSocket === "undefined") {
        alert("socket");
      } else {
        // socket
        this.socket = new WebSocket("ws://127.0.0.1:3000");
        // socket
        this.socket.onopen = this.open;
        // socket
        this.socket.onerror = this.error;
        // socket
        this.socket.onmessage = this.getMessage;
      }
    },
    open() {
      console.log("socket");
      this.send()
    },
    error() {
      console.log("");
    },
    getMessage(msg) {
      let msgChat = JSON.parse(msg.data);
      console.log(msgChat)
      this.list.push(msgChat.container)
      this.config.img = msgChat.container.img
      this.config.name = msgChat.container.name
      this.receiveId = msgChat.id
      console.log(msgChat.id)
    },
    send() {
      console.log(this.userMessage)
      let showObj = {
        type: "show",
        account: this.userMessage.id,
      };
      let params = JSON.stringify(showObj);
      this.socket.send(params);
    },
    close() {
      console.log("socket");
    },
    destroyed() {
      // 
      this.socket.onclose = this.close;
    },
