                this.$message.error({
                    message: '',
                    center: true
                });
            //
            }else if(this.ruleForm.LoginAccount.match(account) == null || this.ruleForm.LoginPassword.match(password) == null || this.ruleForm.NewPassword.match(password) == null){
                this.$message({
                    message: '',
                    type: 'warning',
                    center: true
                });
            }else {
                //
                let url = '/personal/Personal/change';
                let token = localStorage.getItem("tokenVue");
                //php
                let data = {
                    acc:this.ruleForm.LoginAccount,
                    password:this.ruleForm.LoginPassword,
                    newPassword:this.ruleForm.NewPassword,
                    token:token
                };
                sendParam(url, data).then(res => {
                    if(res.data.code==1){
                        //
                        this.$message({
                            message: res.data.msg,
                            type: 'success',
                            center: true
                        });
                        this.ruleForm.LoginAccount = '';
                        this.ruleForm.LoginPassword = '';
                        this.ruleForm.NewPassword = '';
                    }else{
                        //
                        this.$message({
                            message: res.data.msg,
                            type: 'warning',
                            center: true
                        });
                    }
                }).catch(err => {
                    //
                    console.log(err)
                })
            }
        }, 
        //
        gender() {
            if(this.value == ''){
                this.$message.error({
                    message: '',
                    center: true
                });
            }else{
                //
                let url = '/personal/Personal/gender';
                let token = localStorage.getItem("tokenVue");
                //php
                let data = {
                    sex:this.value,
                    token:token
                };
                sendParam(url, data).then(res => {
                    if(res.data.code==1){
                        this.userMessage.sex = res.data.data
                        //
                        this.$message({
                            message: res.data.msg,
                            type: 'success',
                            center: true
                        });
                    }else{
                        //
                        this.$message({
                            message: res.data.msg,
                            type: 'warning',
                            center: true
                        });
                    }
                }).catch(err => {
                    //
                    console.log(err)
                }) 
            }
        },
        //
        addressClick() {
            if(this.ruleForm.address == ''){
                this.$message.error({
                    message: '',
                    center: true
                });
            }else{
                //
                let url = '/personal/Personal/address';
                let token = localStorage.getItem("tokenVue");
                //php
                let data = {
                    add:this.ruleForm.address,
                    token:token
                };
                sendParam(url, data).then(res => {
                    this.userMessage.add = res.data.data
                    if(res.data.code==1){
                        //
                        this.$message({
                            message: res.data.msg,
                            type: 'success',
                            center: true
                        });
                    }else{
                        //
                        this.$message({
                            message: res.data.msg,
                            type: 'warning',
                            center: true
                        });
                    }
                }).catch(err => {
                    //
                    console.log(err);
                })
            }
        },
        //
        avatarClick() {
            //
            let url = '/personal/Personal/avatarClick';
            let token = localStorage.getItem("tokenVue");
            //php
            let data = {
                head_img:this.lastImgUrl,
                token:token
            };
            sendParam(url, data).then(res => {
                if(res.data.code==1){
                    this.userMessage.head_img = res.data.data;
                    //
                    this.$message({
                        message: res.data.msg,
                        type: 'success',
                        center: true
                    });
                }else{
                    //
                    this.$message({
                        message: res.data.msg,
                        type: 'warning',
                        center: true
                    });
                }
            }).catch(err => {
                //
                console.log(err);
            })
        },
        handleChange(val) {
            console.log(val);
        },
        format(percentage) {
            return percentage === 100 ? '' : `${percentage}`;
        },
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    alert('submit!');
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePictureCardPreview(file) {
            this.dialogImageUrl = file.url;
            this.dialogVisible = true;
        },
        // 
        uploadSuccess(response, file, fileList) {
            this.lastImgUrl = response;
        },
        next() {
            if (this.active++ > 2) this.active = 0;
        }
    }
}
</script>

